html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Mulish", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
  background-color: #222831;
}

.container {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1, h2 {
  font-weight: 400;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.4rem;
  font-weight: 400;
}

[class*=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000000;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

/* Sobre TechPRo */
.notavif.notwebp .header {
  background-image: url(../img/bg_header.jpg);
}

.webp .header {
  background-image: url(../img/bg_header.webp);
}

.avif .header {
  background-image: linear-gradient(to bottom, rgba(34, 40, 49, 0.9) 0%, rgba(34, 40, 49, 0.9) 100%), url(../img/bg_header.avif);
}

.header {
  height: 60rem;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
@media (min-width: 768px) {
  .header {
    text-align: left;
  }
}
.header__grid {
  color: #EEEEEE;
  display: flex;
  flex-direction: column;
  max-width: 30rem;
}
@media (min-width: 768px) {
  .header__grid {
    max-width: calc(100% + 2rem);
  }
}
.header__my-name {
  font-weight: 700;
  font-size: 3rem;
}
.header__my-name::before {
  content: "";
  display: block;
  width: 5rem;
  height: 5rem;
  background-image: url(../img/laptop-code-solid.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 5rem;
  margin: 0 auto 2rem auto;
}
@media (min-width: 768px) {
  .header__my-name::before {
    margin: 0 0 2rem 0;
  }
}
.header__my-name::after {
  content: "";
  display: block;
  padding: 0.5rem;
  margin-bottom: 1rem;
  border-bottom: 2px solid #EEEEEE;
}
@media (min-width: 768px) {
  .header__my-name {
    font-size: 6rem;
    max-width: 50rem;
  }
}
.header__text {
  font-size: 1.6rem;
  text-transform: uppercase;
  margin: 0;
}
@media (min-width: 768px) {
  .header__text {
    font-size: 2.5rem;
  }
}
.header__content {
  animation: 1s ease-in-out 0s 1 headerAnimation;
}
@keyframes headerAnimation {
  0% {
    opacity: 0;
    transform: translateY(-10rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.footer {
  color: #EEEEEE;
  padding: 8rem 0;
}
.footer__heading {
  display: inline-block;
  line-height: 1.6;
  font-size: 3rem;
  margin-bottom: 5rem;
  padding: 1rem;
  text-transform: uppercase;
  border-bottom: 1px solid #EEEEEE;
}
.footer__text {
  font-size: 1.8rem;
}

.contacto__grid {
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
}
@media (min-width: 768px) {
  .contacto__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.contacto__link {
  color: #EEEEEE;
  position: relative;
  padding-left: 5rem;
  font-size: 1.8rem;
}
.contacto__link[href*="google.com/maps"]::before {
  content: "";
  position: absolute;
  height: 3rem;
  width: 3rem;
  left: 0;
  top: -0.3rem;
  background-image: url("../img/contact/house.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 2rem;
}
.contacto__link[href*="@gmail.com"]::before {
  content: "";
  position: absolute;
  height: 3rem;
  width: 3rem;
  left: 0;
  top: -0.3rem;
  background-image: url("../img/contact/email.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 2rem;
}
.contacto__link[href*="+57302531357"]::before {
  content: "";
  position: absolute;
  height: 3rem;
  width: 3rem;
  left: 0;
  top: -0.3rem;
  background-image: url("../img/contact/phone.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 2rem;
}
.contacto__link[href*="wa.me"]::before {
  content: "";
  position: absolute;
  height: 3rem;
  width: 3rem;
  left: 0;
  top: -0.3rem;
  background-image: url("../img/contact/whatsapp.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 2rem;
}
.contacto__link[href*="facebook.com"]::before {
  content: "";
  position: absolute;
  height: 3rem;
  width: 3rem;
  left: 0;
  top: -0.3rem;
  background-image: url("../img/contact/facebook.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 2rem;
}
.contacto__link[href*="instagram.com"]::before {
  content: "";
  position: absolute;
  height: 3rem;
  width: 3rem;
  left: 0;
  top: -0.3rem;
  background-image: url("../img/contact/instagram.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 2rem;
}
.contacto__link[href*="github.com"]::before {
  content: "";
  position: absolute;
  height: 3rem;
  width: 3rem;
  left: 0;
  top: -0.3rem;
  background-image: url("../img/contact/github.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 2rem;
}

.education {
  background: #393E46;
  padding: 8rem 0 10rem 0;
  color: #EEEEEE;
}
@media (min-width: 768px) {
  .education__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
    justify-content: center;
    align-items: center;
  }
}
.education__heading {
  display: inline-block;
  line-height: 1.6;
  font-size: 3rem;
  margin-bottom: 5rem;
  padding: 1rem;
  text-transform: uppercase;
  border-bottom: 1px solid #EEEEEE;
}
.education__text {
  line-height: 1.8;
  text-align: justify;
  font-size: 1.7rem;
}
.education__content {
  margin-bottom: 7rem;
}
@media (min-width: 768px) {
  .education__content {
    margin-bottom: 0;
    grid-column: 1/4;
  }
}
@media (min-width: 768px) {
  .education__image-content {
    grid-column: 4/6;
  }
}
.education__image {
  height: 20rem;
  margin: 0 auto;
}
.education__link {
  color: #00ADB5;
}

.certificado {
  padding: 5rem 0 10rem 0;
}
.certificado__heading {
  display: inline-block;
  line-height: 1.6;
  font-size: 3rem;
  margin-bottom: 5rem;
  padding: 1rem;
  text-transform: uppercase;
  border-bottom: 1px solid #EEEEEE;
}
.certificado__grid {
  display: grid;
  grid-template-columns: repeat(2, 30rem);
  column-gap: 4rem;
  overflow-y: hidden;
  scroll-snap-type: x mandatory;
}
@media (min-width: 768px) {
  .certificado__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
.certificado__image {
  scroll-snap-align: center;
  border-radius: 2rem;
}

.skills {
  padding: 10rem 0;
  background-color: #222831;
  color: #EEEEEE;
}
.skills__wave {
  margin-top: -18.5rem;
}
@media (min-width: 768px) {
  .skills__wave {
    margin-top: -25rem;
  }
}
@media (min-width: 1024px) {
  .skills__wave {
    margin-top: -32.5rem;
  }
}
@media (min-width: 768px) {
  .skills__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
    justify-content: center;
    align-items: center;
    grid-auto-flow: dense;
  }
}
.skills__content {
  margin-bottom: 7rem;
}
@media (min-width: 768px) {
  .skills__content {
    grid-column: 3/6;
  }
}
@media (min-width: 768px) {
  .skills__image-content {
    grid-column: 1/3;
  }
}
.skills__image {
  height: 20rem;
  margin: 0 auto;
}
.skills__heading {
  display: inline-block;
  line-height: 1.6;
  font-size: 3rem;
  margin-bottom: 5rem;
  padding: 1rem;
  text-transform: uppercase;
  border-bottom: 1px solid #EEEEEE;
}
.skills__text {
  line-height: 1.8;
  text-align: justify;
  font-size: 1.7rem;
}

.projects {
  background-color: #393E46;
  color: #EEEEEE;
  padding: 10rem 0;
}
.projects__heading {
  display: inline-block;
  line-height: 1.6;
  font-size: 3rem;
  margin-bottom: 5rem;
  padding: 1rem;
  text-transform: uppercase;
  border-bottom: 1px solid #EEEEEE;
}
.projects__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .projects__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .projects__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.project {
  padding: 3rem;
  background: #2e3238;
}
.project__image {
  background: #EEEEEE;
  height: 30rem;
  border-radius: 2rem;
  margin: 0 auto;
}
.project__description {
  line-height: 1.7;
  font-size: 1.7rem;
  text-align: justify;
}
.project__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  margin-top: 5rem;
}
.project__link {
  background-color: #00ADB5;
  color: #000000;
  padding: 1rem;
  border-radius: 1rem;
  font-size: 2rem;
  transition-property: background-color;
  transition-duration: 0.3s;
}
.project__link:hover {
  background-color: #007c82;
}